.TopPanel {
    display: flex;
    width: 100%;
    min-height: 100px;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    border-bottom: 2px solid #efefef;
}

.topPanelSide {
    display: flex;
    flex-direction: column;
    text-align: end;
    padding: 1em;
}

.logo_img{
    height: 55px;
    width: 55px;
    cursor: pointer;
}